<template>
  <div class="article">
  <h3>文章分类</h3>
    <ul class="outerlist">
       <li @click='toggle'>CSS
          <ol v-show='open' class="innerlist">
          <li><a href="/static/art/css/lineheight.html"   target="_Blank">line-height和vertic-align深入理解</a></li>
          <li><a href="/static/art/css/zindex.html"   target="_Blank">z-index深入理解</a></li>
            <li><a href="/static/art/css/marpad.html"   target="_Blank">margin和padding深入理解</a></li>
            <li><a href="/static/art/css/position.html"  target="_Blank">position深入理解</a></li>
            <li><a href="/static/art/css/border.html"  target="_Blank">border深入理解</a></li>
            <li><a href="/static/art/css/float.html"  target="_Blank">float深入理解</a></li>
          </ol>
       </li>
       <li @click='toggle1'>HTML
         <ol v-show='open1' class="innerlist">
           <li><a href="/static/art/html/mesia.html"  target="_Blank">响应式设计</a></li>
         </ol>
       </li>
       <li @click='toggle2'>JavaScript
          <ol v-show='open2' class="innerlist">
            <li><a href="/static/art/js/var.html"  target="_Blank">JavaScript变量和作用域</a></li>
            <li><a href="/static/art/js/proto.html"  target="_Blank">JavaScript对象和原型</a></li>
            <li><a href="/static/art/js/function.html"  target="_Blank">JavaScript函数的使用</a></li>
            <li><a href="/static/art/js/var.html">JavaScript变量和作用域</a></li>
            <li><a href="/static/art/js/pattren.html"  target="_Blank">JavaScript正则表达式</a></li>
            <li><a href="/static/art/js/event.html"  target="_Blank">JavaScript事件</a></li>
            <li><a href="/static/art/js/DOM.html"  target="_Blank">DOM小结</a></li>
            <li><a href="/static/art/js/AJAX.html"  target="_Blank">Ajax小结</a></li>
          </ol>
       </li>
       <li @click='toggle3'>others
       <ol v-show='open3' class="innerlist">
          <li><a href="./static/art/design/design.html"  target="_Blank">设计模式学习</a></li>
        </ol>
        </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'article',
  data(){
  	return{
  		model:[{
        name:'CSS',
        content:[
        {
          title:'边框',
          src:'./border.html'
        },
        {
          title:'浮动',
          src:'./float.html'
        }
        ]
    },
    {
      name:'HTML',
      content:[]
    },
    {
      name:'JavaScript',
      content:[]
    }
    ],
    open:false ,
    open1:false,
    open2:false,
    open3:false
  	}
  },
  methods:{
    toggle(){
      this.open= !this.open
    },
    toggle1(){
      this.open1= !this.open1
    },
    toggle2(){
      this.open2= !this.open2
    },
    toggle3(){
      this.open3= !this.open3
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  li{
  	color: #000;
  }
  li:hover{
    color: green;
  }
  h3{
    text-align: center;
  }
  .article{
    margin-top: 20px;
    margin-left: 10%;
  }
  .outerlist li{
    margin: 10px 0;
  }
  .innerlist li{
    margin: 0;
  }
  a:hover{
    color: green;
  }
</style>



