<template>
  <div class="firgame">
  <div class="label-wrapper">
	  		<span class="icon-firgame"></span>
	  		<span>五子棋</span>
	  	</div>
	<button @click='isshow=!isshow'>{{isshow ? 'close' : 'open'}}</button>
  <div id="oo" v-show='isshow'>
     <canvas id='canvas1' @click='clickle($event)'></canvas>
     <div class="out" v-show='crash' @click='begaingame'>
          <div class="button">开始</div>
      </div>
  </div>
    
  </div>
 
</template>

<script>
export default {
  name: 'firgame',
   data(){
  	return{
  		isshow:false,
      context:'',
      crash:true,
      borad:[],
      wins:[],
      myWin:[],
      comWin:[],
      result:[],
      myScore:[],
      computerScore:[],
      started:false,
      count:0,
      gride:30
  	}
  },
  created(){
    for(var i=0;i<15;i++){
      this.wins[i] = [];
      for(var j = 0;j<15;j++){
        this.wins[i][j] = [];
      }
    };
    for(var i=0;i<15;i++){
      for(var j=0;j<11;j++){
        for(var k=0;k<5;k++){
          this.wins[i][j+k][this.count]=true;
        }
        this.count++;
      }
    };
    for(var i=0;i<11;i++){
      for(var j=0;j<15;j++){
        for(var k=0;k<5;k++){
          this.wins[i+k][j][this.count]=true;
        }
        this.count++;
      }
    };
    for(var i=0;i<11;i++){
      for(var j=0;j<11;j++){
        for(var k=0;k<5;k++){
          this.wins[i+k][j+k][this.count]=true;
        }
        this.count++;
      }
    };
    for(var i=0;i<11;i++){
      for(var j=14;j>3;j--){
        for(var k=0;k<5;k++){
          this.wins[i+k][j-k][this.count]=true;
        }
        this.count++;
      }
    };
  },
  methods:{
      init(){
        //棋盘数组初始化
        for(var i=0;i<=15;i++){
          this.borad[i]=[];
          for(var j=0;j<=15;j++){
            this.borad[i][j]=0;
          }
        };
        this.result = [0,0,0];
        //画棋盘
        this.context.strokeStyle = '#000000';
        for(var i=0;i<15;i++){
          this.context.beginPath();
          this.context.moveTo(15+30*i,15);
          this.context.lineTo(15+30*i,435);
          this.context.moveTo(15,15+30*i);
          this.context.lineTo(435,15+30*i);
          this.context.closePath();
          this.context.stroke(); 
        }
        for(var i=0;i<this.count;i++){
          this.myWin[i] = 0;
          this.comWin[i] = 0;  
        }
        for(var i=0;i<15;i++){
         this.myScore[i] = [];
         this.computerScore[i] = [];
          for(var j=0;j<15;j++){
            this.myScore[i][j]=0;
            this.computerScore[i][j]=0;
          }
        }
        this.started = true;
      },
      draw(x,y,player){
        if(player==0)
          return;
        this.context.beginPath();
        this.context.arc(x*30+15,y*30+15,13,0,Math.PI*2,true);
        this.context.closePath();
        var g = this.context.createRadialGradient(x*30+15+2,y*30+15-2,0,x*30+15,y*30+15,15);
        if(player==1){
          g.addColorStop(0,'#636766');
          g.addColorStop(1,'#0a0a0a');
        }else{
          g.addColorStop(0,'#f9f9f9');
          g.addColorStop(1,'#d1d1d1');
        }
        this.context.fillStyle=g;
        this.context.fill();
      },
      computer(data){
        var x = data[0];
        var y = data[1];
        for (var k=0;k<this.count;k++){
          if(this.wins[x][y][k]){
            this.myWin[k]++;
            this.comWin[k] = -1;
            if(this.myWin[k] == 5){
              this.result = [15,15,1];
              this.crash = true;
            }
          }
        }
        var max=0;
        var u = 0;
        var v = 0;
        if(this.result[2] != 1){
          for(var i=0;i<15;i++){
            for(var j=0;j<15;j++){
              if(this.borad[i][j] == 0){
                for(var k=0;k<this.count;k++){
                  if(this.wins[i][j][k]){
                    if(this.myWin[k] == 1){
                      this.myScore[i][j] += 200;
                    }else if(this.myWin[k]==2){
                      this.myScore[i][j] += 400;
                    }else if(this.myWin[k]==3){
                      this.myScore[i][j] += 2000;
                    }else if(this.myWin[k] == 4){
                      this.myScore[i][j] += 10000;
                    }
                    if(this.comWin[k] ==1 ){
                      this.computerScore[i][j]+=220;
                    }else if(this.comWin[k] == 2){
                      this.computerScore[i][j]+=420;
                    }else if(this.comWin[k] == 3){
                      this.computerScore[i][j]+=2100;
                    }else if(this.comWin == 4){
                      this.computerScore[i][j] += 20000;
                    }
                  }
                }
                if(this.myScore[i][j]>max){
                  max = this.myScore[i][j];
                  u = i;
                  v = j;
                }else if(this.myScore[i][j]==max){
                  if(this.computerScore[i][j]>this.computerScore[u][v]){
                    u = i;
                    v = j;
                  }
                }
                if(this.computerScore[i][j]>max){
                  max = this.computerScore[i][j];
                  u =i;
                  v =j;
                }else if(this.computerScore[i][j]==max){
                  if(this.myScore[i][j]>this.myScore[u][v]){
                    u = i;
                    v = j;
                  }
                }

              }
            }
          }
          for (var k=0;k<this.count;k++){
            if(this.wins[u][v][k]){
              this.comWin[k] ++;
              if(this.comWin[k] == 5){
               this.result[2] = 2;
               this.crash = true;
              }else{
                this.result[2] = 0;
              }
            }
          }
          this.result[0] = u;
          this.result[1] = v;

        }
        this.borad[u][v] = 1;
        this.draw(u,v,1);
        this.started = true;

      },
      begaingame(){
        this.crash=false;
        this.started =true;
        this.context.clearRect(0,0,450,450);
        this.init();
      },
      clickle(event){
         if(!this.started)
          {return;}
        this.gride = event.target.clientWidth/15;
        var x = Math.floor(event.offsetX/this.gride);
        var y = Math.floor(event.offsetY/this.gride);
        if(this.borad[x][y] != 0)
          return;
        this.borad[x][y] = 2;
        this.draw(x,y,2);
        this.started = false;
        this.computer([x,y]);
      }
    },
    mounted(){
      let canvas = document.querySelector('#canvas1');
      this.context = canvas.getContext('2d');
      canvas.width = 450;
      canvas.height = 450;
      
    }
 
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.firgame{
		margin-top: 30px;
	}
	#messageDiv{
	text-align: center;
	font-size: 16px;
	color: #9F9494;
	margin: 0 auto 20px;
}

   .label-wrapper{
	   font-size: 20px;
	   display: inline-block;
	}
	.label-wrapper span{
		vertical-align: middle;

	}
	button{
		margin: 0 0 0 20px;
		outline: none;
		padding: 2px 8px;
		color: #fff;
		cursor: pointer;
		border-radius:5px; 
		background-color: #1AF20C;
		font-size: 18px;
		border-width: 2px;
		vertical-align: middle;
	}
  #oo{
    position: relative;
    width: 70%;
    margin: 10px auto;
    border:1px solid #000;
    font-size: 0;
  }
  #canvas1{
    width: 100%;
  }
  .out{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #000;
    opacity: 0.5;
  }
  .button{
    font-size: 40px;
    color: #fff;
    text-align: center;
  }
</style>


