<template>
  <div class="snackgame" @keydown='clickle($event)'>
  		<div class="label-wrapper">
	  		<span class="icon-snack"></span>
	  		<span>贪食蛇</span>
	  	</div>
  		<button @click='isshow=!isshow'>{{isshow ? 'close' : 'open'}}</button>
  		<div v-show='isshow' id="oo">
  		    <canvas id='game' ></canvas>
  		    <div class="out" v-show='crash' @click='begaingame'>
	    		<div class="message"> </div>
	    		<div class="button">开始</div>
	    		<div class="face">tips: 空格键切换开始与暂停，方向键控制方向</div>
	    	</div>
    	</div> 	
  </div>
</template>

<script>
export default {
  name: 'snackgame',
  data(){
  	return{
  		isshow:false,
  		crash:true,
  		delay:300,
  		gridNum:24,
  		cell:[],
  		snackArray:[],
  		foodArray:[],
  		barrierArray:[],
  		direction:'right',
  		stop:true,
  		timer:'',
  		context:{}
  	}
  },
  methods:{
  	 findBlank(){
  	 	var i = Math.floor(this.gridNum*Math.random());
		var j = Math.floor(this.gridNum*Math.random());
		if(this.cell[i][j] == 0){
			return [i,j]
		}else{
			return this.findBlank()
		}
	},
	init(){
		this.cell = [];
		this.snackArray = [];
		this.foodArray = [];
		this.barrierArray =[];
		for(let i=0;i<this.gridNum;i++){
				this.cell[i]=[];
				for(let j=0;j<this.gridNum;j++){
					this.cell[i][j] = 0;  //空白点值为0
				}
		}
		this.snackArray = [[2,0],[1,0],[0,0]];
		this.snackArray.forEach((item,index) => {
				this.cell[item[0]][item[1]] = 3 //蛇点为3
			})

		for(let i=0;i<10;i++){
				let temp = this.findBlank();
				this.barrierArray.push(temp);
				this.cell[temp[0]][temp[1]] = 2;
			}

		for(let i=0;i<10;i++){
				let temp = this.findBlank();
				this.foodArray.push(temp);
				this.cell[temp[0]][temp[1]] = 1;
			}	

	},
	draw(arr1,arr2,arr3,context){
		let len = arr1.length-1;
		let head_x = arr1[0][0];
		let head_y = arr1[0][1];
		this.context.fillStyle = '#555';
		this.context.fillRect(head_x*20,head_y*20,20,20);
		for(let i=1;i<len;i++){
				let o1 = arr1[i][0];
				let o2 = arr1[i][1];
				this.context.beginPath();
				this.context.arc(o1*20+10,o2*20+10,10,0,Math.PI*2,true);
				this.context.closePath();
				this.context.fillStyle = '#555';
				this.context.fill();
			}
		let trail_x = arr1[len][0];
			let trail_y = arr1[len][1];
			this.context.arc(trail_x*20+10,trail_y*20+10,6,0,Math.PI*2,true);
			this.context.fill();
		let len2 = arr2.length;
	  	  for(let i=0;i<len2;i++){
	  	  	let o1 = arr2[i][0];
	  	  	let o2 = arr2[i][1];
	  	  	this.context.beginPath();
	  	  	this.context.arc(o1*20+10,o2*20+10,10,0,Math.PI*2,true);
	  	  	this.context.closePath();
	  	  	this.context.fillStyle = '#5CF619';
	  	  	this.context.fill();
	  	  }
	  	  //画障碍
	  	  let len3 = arr3.length;
	  	  for(let i=0;i<len3;i++){
	  	  	let o1 = arr3[i][0];
	  	  	let o2 = arr3[i][1];
	  	  	this.context.fillStyle = '#000';
	  	  	this.context.fillRect(o1*20,o2*20,20,20);
	  	  }
	},
	gameloop(){
		    this.context.clearRect(0,0,480,480);
			this.updateData();
			
			if(this.stop == false){
				this.timer =  setTimeout(this.gameloop, this.delay);
			}
			this.draw(this.snackArray,this.foodArray,this.barrierArray);
	},
	removeEl(arr,value){
		for(let i=0;i<arr.length;i++){
				if((arr[i][0] == value[0])&&(arr[i][1] == value[1])){
					arr.splice(i,1);
					break;
				}
			}
	},
	updateData(){
		let head_x = this.snackArray[0][0];
		let head_y = this.snackArray[0][1];
		let leng = this.snackArray.length;
		//蛇尾位置
		let trail_x = this.snackArray[leng-1][0];
		let trail_y = this.snackArray[leng-1][1];
		switch(this.direction){
			case 'right':
			head_x += 1;
			break;
			case 'up':
			head_y -= 1;
			break;
			case 'down':
			head_y += 1;
			break;
			case 'left':
			head_x -= 1;
		};
		if((head_x>23)||(head_y>23)||(head_x<0)||(head_y<0)||this.cell[head_x][head_y]===2){
			this.crash = true; //撞墙
			this.stop = true;
		}else if(this.cell[head_x][head_y]==3){
			this.crash = true; //撞蛇
			this.stop = true;
		}else if(this.cell[head_x][head_y]==1){
			this.removeEl(this.foodArray,[head_x,head_y]); //吃食物
			this.snackArray.unshift([head_x,head_y]);
			this.cell[head_x][head_y] = 3;
			//加一个食物
			let temp1 = this.findBlank();
			this.foodArray.push(temp1);
			this.cell[temp1[0]][temp1[1]] = 1;

		}else{
			this.snackArray.unshift([head_x,head_y]);
			this.snackArray.pop();
			this.cell[head_x][head_y] = 3;
			this.cell[trail_x][trail_y] = 0;
		}
	},
	begaingame(){
		this.stop = false;
		this.crash = false;
		this.direction = 'right';
		this.context.clearRect(0,0,480,480);
		this.init();
	    this.draw(this.snackArray,this.foodArray,this.barrierArray,this.context);
		this.timer = setTimeout(this.gameloop,this.delay);
		
	},
	clickle(event){
		event.preventDefault();
	    event.returnValue = false;
		switch(event.keyCode){
			case 38:
			if(this.direction !== 'down'){
				this.direction = 'up'
			}
			break  
			case 37:
			if(this.direction !=='right'){
				this.direction = 'left'
			}
			break
			case 39:
			if(this.direction !=='left'){
				this.direction = 'right'
			}
			break
			case 40:
			if(this.direction !=='up'){
				this.direction = 'down'
			}
			break
			case 32: //空格键切换暂停开始
			if(this.stop){
				this.stop = false;
				this.timer = setTimeout(this.gameloop,this.delay);
			}else{
				this.stop = true;
				clearTimeout(this.timer);
			}
		}
	}
	
  },
  mounted(){
  	let canvas = document.querySelector('#game');
  	canvas.width = 480;
	canvas.height = 480;
	let context = canvas.getContext('2d');
	this.context = context;
	// document.body.addEventListener('keydown',(event) => {
 //  	    	event.preventDefault();
 //  	    	event.returnValue = false;
	// 		switch(event.keyCode){
	// 			case 38:
	// 			if(this.direction !== 'down'){
	// 				this.direction = 'up'
	// 			}
	// 			break  
	// 			case 37:
	// 			if(this.direction !=='right'){
	// 				this.direction = 'left'
	// 			}
	// 			break
	// 			case 39:
	// 			if(this.direction !=='left'){
	// 				this.direction = 'right'
	// 			}
	// 			break
	// 			case 40:
	// 			if(this.direction !=='up'){
	// 				this.direction = 'down'
	// 			}
	// 			break
	// 			case 32: //空格键切换暂停开始
	// 			if(this.stop){
	// 				this.stop = false;
	// 				this.timer = setTimeout(this.gameloop,this.delay);
	// 			}else{
	// 				this.stop = true;
	// 				clearTimeout(this.timer);
	// 			}
	// 		}
	// 	})
  }
  
}
 </script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.snackgame{
		
	}
	#game{
		
		
		width: 100%;
	}
	.label-wrapper{
	   font-size: 20px;
	   display: inline-block;
	}
	.label-wrapper span{
		vertical-align: middle;

	}
	button{
		margin: 0 0 0 20px;
		outline: none;
		padding: 2px 8px;
		color: #fff;
		cursor: pointer;
		border-radius:5px; 
		background-color: #1AF20C;
		font-size: 18px;
		border-width: 2px;
		vertical-align: middle;
	}
	.face{
		font-size: 12px;
		color: #fff;
		text-align: center;
	}
	#oo{
		position: relative;
		width: 70%;
		margin: 10px auto;
		border:1px solid #000;
		font-size: 0;
	}
	.out{
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background-color: #000;
		opacity: 0.5;
	}
	.button{
		font-size: 40px;
		color: #fff;
		text-align: center;
	}
	.message{
		font-size: 40px;
		color: red;
		text-align: center;
		height: 40px;
	}
</style>


